@import url('https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap');

*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root{
  min-height: 100%;
}

body{
  background: #000 url('./assets/background.jpg');
  background-size: cover;
  -webkit-font-smoothing: antialiased !important;
}

body, input, button {
  font-family: 'Roboto', Arial;
  font-size: 14px;
}

hr{
  margin-top:10px;
  margin-bottom: 10px;
}

.container{
  margin: 80px auto 0;
  max-width: 480px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content{
  width: 100%;
  background: #FFF;
  margin-top: 50px;
  border-radius: 4px;
  padding: 30px;
}

.content > p{
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 30px;
}

.content  form{
  display: flex;
  flex-direction: column;
}

.content  form label{
  font-size: 14px;
  color: #444;
  font-weight: bold;
  margin-bottom: 8px;
}


.content  form input{
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 2px;
  height: 45px;
  padding: 0 15px;
  font-size: 16px;
}
.btn{
  border: 0;
  border-radius: 2px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: bold;
  background: #f05a5b;
  color: #fff;
  cursor: pointer;
}

.btn-secondary{
  border: 0;
  border-radius: 2px;
  width: 100%;
  height: 42px;
  padding: 0 20px;
  font-size: 16px;
  font-weight: bold;
  /* background: #7B68EE; */
  /* color: #fff; */
  cursor: pointer;
}

.content button.btn:hover{
  background: #e14f50;
}

.details{
  margin-top:21px;
  color: blue
}

.details-emp{
  font-size: 20px;
  color:black;
  margin-bottom: 10px;
  list-style: none;
}

.form-search{
  width: 100%;
  margin-bottom: 20px;
  border-top:none;
  border-left:none;
  border-right:none;
  border-bottom: 1px solid #ddd;
  border-radius: 2px;
  height: 35px;
  padding: 0 15px;
  font-size: 16px;
}